'use strict';

// MODULES //

import PINF from '@stdlib/constants-float64-pinf' ;
import NINF from '@stdlib/constants-float64-ninf' ;


// MAIN //

/**
* Revives a JSON-serialized number.
*
* @param {string} key - key
* @param {*} value - value
* @returns {*} revived value
*
* @example
* import parseJSON from '@stdlib/utils-parse-json' ;
*
* var str = '{"type":"float64","value":"NaN"}';
*
* var v = parseJSON( str, reviver );
* // returns NaN
*/
function reviver( key, value ) {
	if (
		value !== null &&
		typeof value === 'object' &&
		value.type === 'float64'
	) {
		if ( value.value === 'NaN' ) {
			return NaN;
		}
		if ( value.value === '+Infinity' ) {
			return PINF;
		}
		if ( value.value === '-Infinity' ) {
			return NINF;
		}
	}
	return value;
}


// EXPORTS //

export default reviver;
